export default async function seed(db) {
  const tasks = [{name: "Taste Javascript", complete: true},
                 {name: "Buy a unicorn",    complete: false}];

  let table = db.getSchema().table("Task");
  return db.insertOrReplace().into(table).values(tasks.map(table.createRow)).exec();
}



